.navbar-bg{
    width: 100%;
    min-height: 84px;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: end;
    padding-left: 50px;
    padding-right: 50px;
    position: sticky;
    top: 0;
}

.container-of-title-btn{
    display: flex ;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.title-of-nav{
    font-family: Sans-serif;
    font-size: 28px;
    color: #343C6A;
}



.ul-of-nav{
    display: flex;
    margin-top: 0px;
    width: 500px;
    padding: 0px;
    margin-bottom: 0px;
}

.list-of-nav{
    font-family: Sans-serif;
    font-size: 16px;
    color: #718EBF;
    margin-right: 40px;
    list-style: none;
    padding: 10px;
    cursor: pointer;
}

.actibe-tab-nav{
    border-width: 0px;
    border-bottom-width: 5px;
    border-color: #2D60FF;
    border-style: solid;
}