.container-of-btn{
    width: 166px;
    height: 36px;
    border-radius: 12px;
    background-color: #2D60FF;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.icon-of-btn{
    width: 15px;
    height: 15px;
    color: #ffffff;
    margin-right: 10px;
}

.text-of-btn{
    font-family: Sans-serif;
    font-size: 15px;
    color: #ffffff;
}



.popup-content{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0c0e11;
    opacity: 0.9;
}

.PopupContainer-Add-transactions{
  background-color: #ffffff;
  min-height: 200px;
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  padding: 15px;
  
  
}

.right-container-of-Add-transactions{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    
}



.heading-paragraph-container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.cancel-icon{
    width: 24px;
    height: 24px;
    cursor: pointer;
}

.HeadingPopup{
    font-family: Sans-serif;
    font-size: 20px;
    color: '#333B69';
    margin-bottom: 5px;
  }

.paragraph-popup{
    font-family: Sans-serif;
    font-size: 14px;
    color: #505887;
    margin-bottom: 0px;
    
}
.form-container-add-transaction{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lable-popup{
    font-family: Sans-serif;
    font-size: 16px;
    color: #505887;
    margin-bottom: 5px;
    align-self: flex-start;
}

.input-popup{
    width: 418px;
    height: 50px;
    border-radius: 15px;
    font-family: Sans-serif;
    font-size: 15px;
    color: #718EBF;
    padding: 10px;
    padding-left: 15px;
    border: 1px solid #DFEAF2;
    margin-bottom: 10px;
    outline: none;
}

.input-popup::-webkit-outer-spin-button,
.input-popup::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.Addbtn{
  width: 416px;
  height: 36px;
  border-width: 0px;
  border-radius: 8px;
  display: flex;
  background-color: #2D60FF ;
  justify-content: center;
  align-items: center;
  font-family: Sans-serif;
  font-size: 14px;
  color: #ffffff;
  font-weight: bold;
  cursor: pointer;
  outline: none;
}
.error-msg{
    font-family: Sans-serif;
    font-size: 15px;
    color:red;
    align-self:flex-start;
}